import React, { Component } from 'react';
import CountdownNow from 'react-countdown-now';
import api from '../../services/api';

import '../../css/Manage.css';

class Manage extends Component {

  state = {
    clock: 10,
    customTime: 1,
    customTimeString: '1 Min'
  }

  constructor(props) {
    super(props);
    this.countdown = React.createRef();
  }

  componentDidMount() {

  }

  async changeCountdownTime(time) {
    this.changeCountdownTimeServer(time)
    await this.setState({ clock:  time })
    this.countdown.current.start()
  }

  async changeCountdownTimeServer(time) {
    try {
      await api.post('/', { countdown: time })
    } catch (err) {
      console.log(err)
    }
  }

  customTimePlus() {
    const customTime = this.state.customTime + 1
    const customTimeString = `${customTime} Min`
    this.setState({ customTime, customTimeString })
  }

  customTimeMinus() {
    const customTime = this.state.customTime - 1
    if (customTime !== -1) {
      const customTimeString = `${customTime} Min`
      this.setState({ customTime, customTimeString })
    }
  }

  customTimeBlur() {
    this.setState({ customTimeString: `${this.state.customTime} Min` })
  }

  customTimeApply() {
    this.changeCountdownTime(this.state.customTime * 60)
  }

  handleChangeCustomTime = e => {
    const time = Number.parseInt(e.target.value)
    if (Number.isInteger(time)) {
      this.setState({ customTime: time, customTimeString: time })
    } else if (isNaN(time) && e.target.value === '') {
      this.setState({ customTime: 0, customTimeString: '' })
    }
  }
  

  render() {
    return (
      <div className="content">
        <section id="manage">
          <div className="clock clock-manage">
            <CountdownNow date={ Date.now() + this.state.clock * 1000 } ref={this.countdown} />
          </div>
          <div className="countdown-time">
          <button className="btn btn-green" onClick={() => this.changeCountdownTime(0)}>0 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 5)}>5 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 10)}>10 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 15)}>15 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 20)}>20 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 25)}>25 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 30)}>30 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 35)}>35 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 40)}>40 Min</button>
            <button className="btn btn-green" onClick={() => this.changeCountdownTime(60 * 45)}>45 Min</button>
          </div>
          <div className="countdown-time-custom">
            <button className="btn-plus" onClick={() => this.customTimePlus()}>+</button>
            <input className="input-time-custom" value={this.state.customTimeString}
              onChange={this.handleChangeCustomTime}
              onClick={() => this.setState({ customTimeString: '' })}
              onBlur={() => this.customTimeBlur()}
            />
            <button className="btn-minus" onClick={() => this.customTimeMinus()}>-</button>

            <button className="btn btn-green btn-apply" onClick={() => this.customTimeApply()}>Aplicar</button>            
          </div>

        </section>
      </div>
    );
  }

}

export default Manage;
