import React, { Component, Fragment } from 'react';
import socketIo from 'socket.io-client';
import CountdownNow from 'react-countdown-now';
import api, { API_URL } from '../../services/api';

import '../../css/Countdown.css';

class Countdown extends Component {

  state = {
    clock: 10
  }

  constructor(props) {
    super(props);
    this.countdown = React.createRef();
  }

  componentDidMount() {
    this.registerToSocket()
  }

  registerToSocket = () => {
    const socket = socketIo(API_URL);

    socket.on('new-coutdown', async (data) => {
      await this.setState({ clock:  data.countdown })
      this.countdown.current.start()
    })
  }

  async restartClock() {
    try {
      await api.get('/')
      console.log('Success')
    } catch (err) {
      console.log(err)
    }
  }

  async stopClock() {
    try {
      await api.get('/stop')
      console.log('Success')
    } catch (err) {
      console.log(err)
    }
  }

  render() {
    return (
      <Fragment>
        <div className="clock">
          <CountdownNow date={ Date.now() + this.state.clock * 1000 } ref={this.countdown} />
        </div>
        <div className="invisible" onClick={() => this.restartClock()}>Restart</div>
        <div className="invisible" onClick={() => this.stopClock()}>Stop</div>
      </Fragment>
    );
  }

}

export default Countdown;
